
.review {
  width: 100%;
  height: 100%;
  overflow: scroll;
  overflow-x: hidden;
  position: relative;
  padding-top: .24rem;
  -webkit-overflow-scrolling: touch;
}
.review .review-list {
  width: 100%;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  text-align: left;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
     -moz-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0 .24rem 0;
}
.review .review-list .review-item {
  width: 48%;
  margin-bottom: .24rem;
  display: block;
  background: #fff;
  cursor: pointer;
}
.review .review-list .review-item img {
  width: 100%;
}
.review .review-list .review-item .title {
  width: 100%;
  height: 1.14rem;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  padding: .16rem;
  color: #3D3D3D;
  line-height: .4rem;
  font-size: .28rem;
}
.review .review-list .review-item .title .title-content {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
.review .review-list .review-item .title .time {
  display: block;
  font-size: .24rem;
  color: #9B9B9B;
  line-height: .34rem;
}
.review .loading {
  width: 100%;
  height: .88rem;
  color: #989898;
  line-height: .88rem;
  text-align: center;
}
.review .loading img {
  height: 50%;
  vertical-align: middle;
  margin-right: .1rem;
}
.review .nomore {
  width: 100%;
  padding: .2rem 0;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  text-align: center;
  color: #989898;
}
.review .nodata {
  width: 100%;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
     -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
       -o-transform: translateY(-50%);
          transform: translateY(-50%);
}
.review .nodata img {
  width: 60%;
  margin: 0 auto;
}
.review .loadBig {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.review .loadBig .load {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
       -o-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  color: #989898;
}
.review .loadBig img {
  width: 50%;
  margin-bottom: .3rem;
}



