
.home{
  width: 100%;
  height: 100%;
  position: relative;
  padding-top: .82rem;
  padding-bottom: 1rem;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
}
.home .head{
  position: fixed;
  top:0;
  left: 0;
}
.head{
  width: 100%;
  height: 0.82rem;
  line-height: .82rem;
  background: #fff;
  overflow-x: scroll;
  font-size: .28rem;
  z-index: 999;
}
.head::-webkit-scrollbar {display:none
}
.head-content{
  width: 100%;
}
.head .head-item{
  margin: 0 .5rem;
  text-align: center;
  display: inline-block;
  color:#9B9B9B;
  background: #fff;
}
.head .head-item.active{
  color: #070A22;
  font-weight: bold;
}
.home .list{
  width: 100%;
  height: 100%;
  margin-top: .08rem;
}
.footer{
  width: 100%;
  position: fixed;
  z-index: 999;
  background:rgba(250,250,250,0.9);
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  bottom: 0;
  left: 0;
  font-size: .2rem;
  height: 1rem;
}
.footer a{
  display: block;
  -webkit-flex: 1;
     -moz-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  cursor: pointer;
}
.footer a img{
  display: block;
  margin: 0 auto;
  width: .99rem;
}



