
.matchList {
  margin-top: .08rem;
  height: 100%;
  overflow: hidden;
}
.matchList .list {
  overflow: scroll;
  padding-bottom: .4rem;
  -webkit-overflow-scrolling: touch;
}
.matchList .match-item {
  width: 100%;
  background: #fff;
  padding-bottom: .24rem;
  margin-bottom: .08rem;
}
.matchList .match-item .title {
  width: 100%;
  font-size: .24rem;
  color: #9B9B9B;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  padding: .16rem .32rem;
  text-align: left;
  position: relative;
  margin-bottom: .08rem;
}
.matchList .match-item .title span:nth-last-of-type(1) {
  position: absolute;
  right: .32rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
     -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
       -o-transform: translateY(-50%);
          transform: translateY(-50%);
}
.matchList .match-item .team-info {
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
     -moz-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #202020;
}
.matchList .match-item .team-info .team {
  font-size: .26rem;
  font-weight: bold;
  width: 30%;
}
.matchList .match-item .team-info .team img {
  width: .56rem;
  height: .56rem;
  -moz-border-radius: 100%;
       border-radius: 100%;
  display: block;
  margin: 0 auto;
}
.matchList .match-item .team-info .team div {
  width: 100%;
  height: .5rem;
  line-height: .5rem;
  overflow: hidden;
  text-align: center;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.matchList .match-item .team-info .status {
  width: 2.2rem;
  font-weight: bold;
  font-size: .26rem;
}
.matchList .match-item .team-info .status .status-name {
  font-weight: normal;
  width: 1rem;
  padding: .1rem;
  -moz-border-radius: .04rem;
       border-radius: .04rem;
  margin: .16rem auto 0;
  font-size: .2rem;
  color: #3d3d3d;
  background: rgba(155, 155, 155, 0.23);
}
.matchList .match-item .team-info .status .status-name.red {
  background: rgba(255, 153, 166, 0.23);
}
.matchList .nodata {
  width: 100%;
  margin-top: 50%;
}
.matchList .nodata img {
  width: 50%;
  margin: 0 auto;
}
.matchList .nomore {
  width: 100%;
  padding: .2rem 0;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  text-align: center;
  color: #989898;
}
.matchList .loading {
  width: 100%;
  height: .88rem;
  color: #989898;
  line-height: .88rem;
}
.matchList .loading img {
  height: 50%;
  vertical-align: middle;
  margin-right: .1rem;
}
.matchList .loadBig {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.matchList .loadBig .load {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
       -o-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  color: #989898;
}
.matchList .loadBig img {
  width: 50%;
  margin-bottom: .3rem;
}



