* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*{-webkit-tap-highlight-color:transparent;-webkit-tap-highlight-color:transparent;}
[v-cloak]{display: none!important;}
@media screen and (orientation: landscape) {
  html {
    font-size:50px!important;
  }
  #bottomImg{
    height: 58px;
  }
}
@media screen and (min-width: 700px) and (max-width: 1000px) {
  html {
    font-size:75px!important;
  }
}
@media screen and (min-width: 1000px) {
  html {
    font-size:100px!important;
  }
}

html {
  overflow-y: scroll;
  /*overflow-x: hidden;*/
  -webkit-text-size-adjust: none;
  _background-attachment: fixed;
  -webkit-overflow-scrolling: touch;
  height: 100%;
}

body, dl, dd, ul, ol, h1, h2, h3, h4, h5, h6, form, p {
  margin: 0;
}

body {
  width: 100%;
  background: rgb(244, 244, 244);
  line-height: 1;
  color: rgb(0, 0, 0);
  height: 100%;
  font-size: 12px;
  font-family: "Microsoft YaHei", PingFangSC, Arial, "宋体", Helvetica, sans-serif;
  text-align: center;
}

ol, ul {
  padding: 0;
}

h1, h2, h3, h4, h5 {
  font-size: inherit;
  font-weight: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0
}

ul, ol, li {
  list-style: none;
}

img {
  border: 0;
  -ms-interpolation-mode: bicubic;
  vertical-align: top;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

em {
  margin: 0;
  padding: 0;
  border: 0;
  font: 12px normal
}

/* 默认链接 */
a {
  display: block;
  text-decoration: none;
  color: rgb(0, 0, 0);
  outline: none; /* FF Opera */
  position: relative;
  -webkit-tap-highlight-color: transparent; /* 给移动端页面加上:active伪类,js中给body绑定touchstart事件，处理函数为空即可 */
}

a:hover {
  text-decoration: none;
}
.unselect {
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;

  /* 以下两个属性目前并未支持，写在这里为了减少风险 */
  -o-user-select: none;
  user-select: none;
}


/* 左右悬浮 */
.f_l {
  float: left;
}

.f_r {
  float: right;
}

/* 清除悬浮 */
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.flexBox {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

.flex-1 {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
}

.table-container {
  display: table;
  width: 100%;
  height: 100%;
}

.cell-container {
  display: table-cell;
  height: 100%;
  vertical-align: middle;
  text-align: center;
}



